@import '../SCSS/custom.css';



/* Define la animacin */
@keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Aplica la animacin al elemento */
.animacion-entrada {
    animation: fadeInUp 0.5s ease-out;
  }
  

/* Estilos para pantallas grandes (md y ms grandes) */
@media (min-width: 768px) {
    .col-md-6 {
        text-align: left;  /* Alinea el texto a la izquierda en pantallas grandes */
    }
}

/* Estilos para pantallas ms pequeas (md y menores) */
@media (max-width: 767px) {
    .col-md-6 {
        text-align: center;  /* Alinea el texto al centro en pantallas pequeas */
    }
}

.rotated{
    transform: rotate(-5deg);
}

